import React, { useContext, useEffect, useState } from 'react';
import { Container, Row, Col } from 'react-bootstrap';
import Fade from 'react-reveal/Fade';
import Tilt from 'react-tilt';
import PortfolioContext from '../../context/context';
import Title from '../Title/Title';
import ProjectImg from '../Image/ProjectImg';

const isExternalLink = (url) => {
  return url.startsWith('http') || url.startsWith('www');
};

const Projects = () => {
  const { projects } = useContext(PortfolioContext);

  const [isDesktop, setIsDesktop] = useState(false);
  const [isMobile, setIsMobile] = useState(false);

  useEffect(() => {
    if (window.innerWidth > 769) {
      setIsDesktop(true);
      setIsMobile(false);
    } else {
      setIsMobile(true);
      setIsDesktop(false);
    }
  }, []);

  return (
    <section id="projects">
      <Container>
        <div className="project-wrapper">
          <Title title="Projects" />
          {projects.map((project) => {
            const { title, subtitle, projectCount, info, info2, url, repo, img, id } = project;
            const target = isExternalLink(url) ? '_blank' : '_self';

            return (
              <Row key={id}>
                <Col lg={4} sm={12}>
                  <Fade
                    left={isDesktop}
                    bottom={isMobile}
                    duration={1000}
                    delay={500}
                    distance="30px"
                  >
                    <div className="project-wrapper__text">
                      <h3 className="project-wrapper__text-title">{title || 'Project Title'}</h3>
                      <h4 className="project-wrapper__text-subtitle">{subtitle || ''}</h4>
                      <h4 className="project-wrapper__text-projectCount">{projectCount || ''}</h4>

                      {/* Conditionally render image for mobile view */}
                      {isMobile && (
                        <div className="project-wrapper__image">
                          <Tilt
                            options={{
                              reverse: false,
                              max: 8,
                              perspective: 1000,
                              scale: 1,
                              speed: 300,
                              transition: true,
                              axis: null,
                              reset: true,
                              easing: 'cubic-bezier(.03,.98,.52,.99)',
                            }}
                          >
                            <div data-tilt className="thumbnail rounded">
                              <ProjectImg alt={title} filename={img} />
                            </div>
                          </Tilt>
                        </div>
                      )}

                      <div>
                        <p className="project-info">
                          {info ||
                            'Lorem ipsum dolor sit, amet consectetur adipisicing elit. Excepturi neque, ipsa animi maiores repellendu distinctioaperiam earum dolor voluptatum consequatur blanditiis inventore debitis fuga numquam voluptate architecto itaque molestiae.'}
                        </p>
                        <p className="mb-4 project-info2">{info2 || ''}</p>
                      </div>

                      {url && (
                        <a
                          rel="noopener noreferrer"
                          className="cta-btn cta-btn--hero"
                          href={url}
                          target={target}
                        >
                          See more
                        </a>
                      )}

                      {repo && (
                        <a
                          target="_blank"
                          rel="noopener noreferrer"
                          className="cta-btn text-color-main"
                          href={repo}
                        >
                          Source Code
                        </a>
                      )}
                    </div>
                  </Fade>
                </Col>

                {/* Conditionally render image for desktop view */}
                {isDesktop && (
                  <Col lg={8} sm={12}>
                    <Fade
                      right={isDesktop}
                      bottom={isMobile}
                      duration={1000}
                      delay={1000}
                      distance="30px"
                    >
                      <div className="project-wrapper__image">
                        {url ? (
                          <a
                            href={url}
                            target={target}
                            aria-label="Project Link"
                            rel="noopener noreferrer"
                          >
                            <Tilt
                              options={{
                                reverse: false,
                                max: 8,
                                perspective: 1000,
                                scale: 1,
                                speed: 300,
                                transition: true,
                                axis: null,
                                reset: true,
                                easing: 'cubic-bezier(.03,.98,.52,.99)',
                              }}
                            >
                              <div data-tilt className="thumbnail rounded">
                                <ProjectImg alt={title} filename={img} />
                              </div>
                            </Tilt>
                          </a>
                        ) : (
                          <div>
                            <Tilt
                              options={{
                                reverse: false,
                                max: 8,
                                perspective: 1000,
                                scale: 1,
                                speed: 300,
                                transition: true,
                                axis: null,
                                reset: true,
                                easing: 'cubic-bezier(.03,.98,.52,.99)',
                              }}
                            >
                              <div data-tilt className="thumbnail rounded">
                                <ProjectImg alt={title} filename={img} />
                              </div>
                            </Tilt>
                          </div>
                        )}
                      </div>
                    </Fade>
                  </Col>
                )}
              </Row>
            );
          })}
        </div>
      </Container>
    </section>
  );
};

export default Projects;
